@import "styles/theme/config";

$rootBackground: white !default;
$rootFontSize: 14px !default;
$rootParagraphHeight: 20px;
$rootLineHeight: $rootParagraphHeight / $rootFontSize;

$defaultFont: "Helvetica Neue", Helvetica, "MICROSOFT YaHei", Arial, Tahoma, sans-serif !default;
$codeFont: Courier, "Courier New", monospace !default;
$placeholderColor: #D7D7D7 !default;


/**
 *  application config
 */

// 全局颜色
$greenColor: #42B63D;
$redColor: #FD504E;
$darkColor: #363636;
$lightColor: #999999;


//= header
$headerHeight: 50px;
$headerBackground: #363636;
$headerColor: white;
$headerFontSize: 18px;
$headerBackWidth: 50px;

//= loading
$loadingHeight: 40px;
$loadingBackground: white;
$loadingColor: #A0A0A0;
$loadingSpinColor: #878787;
$loadingFinishedBackground: #FAFAFA;

//= nav
$navHeight: 40px;
$navBackground: #F8F8F8;
$navColor: $darkColor;
$navFontSize: 14px;
$navActiveColor: $greenColor;
$navDotColor: #EE341C;
$navBorder: 1px solid #EDEDED;
$navActiveBorder: 2px solid $navActiveColor;


@import "./libs/func";
@import "./libs/mixin";
@import "./libs/placeholder";



