@import 'styles/config';

.header {
  background: $headerBackground;
  color: $headerColor;
}

.back {
  display: block;
  width: $headerBackWidth;
  height: $headerHeight;

  &:before {
    color: $headerColor;
    line-height: $headerHeight;
    width: 100%;
    margin: 0;
    font-size: $headerFontSize;
  }
}

.title {
  line-height: $headerHeight;
  text-align: center;
  @include text-ellipsis;

  margin-top: - $headerHeight;
  padding: 0 $headerBackWidth + 5px;
  font-size: $headerFontSize;
}



