@import 'styles/config';

.loading {
  height: $loadingHeight;
  line-height: $loadingHeight;
  text-align: center;
  background: $loadingBackground;
  color: $loadingColor;

  &.finished {
    background: $loadingFinishedBackground;
  }
}

.spin:before {
  color: $loadingSpinColor;
  line-height: $loadingHeight;
  font-size: 14px;
}



