
.item {
  background-color: white;
  padding: 16px;
  margin-bottom: 8px;
  border-bottom: 1px solid #EDEDED;

  &:last-child {
    margin-bottom: 0;
    border-bottom: none;
  }
}

.cover {
  width: 70px;
  height: 70px;
}

.body {
  margin-left: 15px;
  margin-top: -3px;
}

.link {
  display: block;
}

.title {
  color: #333333;
  font-size: 15px;
  line-height: 23px;
  max-height: 46px;
}

.time {
  color: #999999;
  font-size: 11px;
  margin: 3px 0 4px;
}

.content {
  color: #999999;
  font-size: 13px;
  line-height: 18px;
  max-height: 36px;
}




