@font-face {
  font-family: 'icon-font';
  src: url('./icons/font.eot?39530147');
  src: url('./icons/font.eot?39530147#iefix') format('embedded-opentype'),
  url('./icons/font.woff?39530147') format('woff'),
  url('./icons/font.ttf?39530147') format('truetype'),
  url('./icons/font.svg?39530147#hui-font') format('svg');
  font-weight: normal;
  font-style: normal;
}

.spin {
  animation: spin 2s infinite linear;
  display: inline-block;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(359deg); }
}

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "icon-font";
  font-style: normal;
  font-weight: normal;
  speak: none;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-carat-w:before { content: '\e800'; }
.icon-spin5:before { content: '\e801'; }
.icon-checked:before { content: '\e802'; }



