
.item {
  background-color: white;
  padding: 16px 16px 20px;

  border-bottom: 1px solid #ECECEC;
  margin-bottom: 8px;

  &:last-child {
    border-bottom: none;
    margin-bottom: 0;
  }
}

.follows {
  display: block;
}

.follow {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 0;

  & + .follow {
    margin-left: 10px;
  }
}

.content {
  color: #333;
  font-size: 15px;
  margin: 6px 0 8px;

  &:last-child {
    margin-bottom: 0;
  }
}

.grayBackground {
  background-color: #F2F2F2;
  color: #666666;
  padding: 10px;
}
.title {
  font-size: 13px;
  max-height: 36px;
  line-height: 18px;
}

.time {
  display: inline-block;
  font-size: 11px;
  color: #999999;
}

.user {
  display: flex;
  .avatar {
    width: 36px;
  }
  .avatarImg {
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }

  .detail {
    flex: 1;
    margin-left: 12px;
  }

  .username {
    font-size: 14px;
    line-height: 20px;
    color: #42B63D;
  }

  .pubtime {
    line-height: 12px;
    font-size: 11px;
    color: #999999;
  }
}





