.item {
  display: block;
  position: relative;
  padding: 13px 16px;
  background: white;

  &:before {
    display: block;
    content: '';
    position: absolute;
    left: 16px;
    right: 16px;
    top: 0;
    height: 1px;
    background: #EDEDED;
  }

  &:first-child:before {
    background: transparent;
  }
}

.title {
  cursor: pointer;
  font-size: 15px;
  line-height: 23px;
  color: #333333;
}

.time {
  display: inline-block;
  font-size: 11px;
  margin-top: 3px;
  color: #999999;
}



