@import 'styles/config';

.nav {
  height: $navHeight;
  background: $navBackground;
  @include user-select(none);
}

.link {
  position: relative;
  display: block;
  cursor: pointer;
  height: $navHeight;
  line-height: $navHeight;
  border-bottom: $navBorder;

  text-align: center;
  color: $navColor;
  font-size: $navFontSize;
}

.dot .link {
  &:after {
    display: inline-block;
    content: '';
    position: absolute;
    top: 12px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin-left: 4px;
    background: $navDotColor;
  }
}

.active {
  pointer-events: none;
  color: $navActiveColor;
  border-bottom: $navActiveBorder;
}



