.rounded {
  border-radius: 50%;
}

.hidden, [hidden] {
  display: none !important;
}

.invisible, [invisible] {
  visibility: hidden !important;
}

.disabled, [disabled] {
  cursor: default !important;
  pointer-events: none !important;

  a, .btn {
    cursor: default !important;
    pointer-events: none !important;
  }
}

/*
 触发硬件加速
 With hardware acceleration supported and enabled, animated translation, rotation, scaling and opacity will definitely be smoother with GPU compositing.
*/
.hw-accelerate {
  transform: translateZ(0);
}

/*
  禁止选择，对 input 输入框无效
  禁止用户选择文字，不禁止的话，用户在手机上点击的话，很容易选择上上面的文字
*/
.no-select {
  @extend %no-select;
}

.no-events {
  pointer-events: none;
}

/* 强制文本换行 */
.break-word, .break-word td, .break-word th {
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-all;
}

table.break-word {
  table-layout: fixed;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.clearfix {
  @extend %clearfix;
}

.center-block {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.h-center, .h-center-before:before, .h-center-after:after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.v-center, .v-center-before:before, .v-center-after:after {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.hv-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.fill-absolute {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.text-ellipsis {
  @extend %text-ellipsis;
}

.text-hide {
  @extend %text-hide;
}

.text-indent {
  text-indent: 2em;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

[data-clamp] {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
}
@for $i from 1 through 4 {
  [data-clamp="#{$i}"] {
    -webkit-line-clamp: $i;
  }
}

.flex {
  display: flex;
}

@for $i from 1 through 4 {
  [data-flex="#{$i}"] {
    flex: $i;
  }
}



